import React from 'react'
import PropTypes from 'prop-types'

const Footer = (props) => (
    <footer id="footer" style={props.timeout ? {display: 'none'} : {}}>
        <p className="copyright">&copy; Matthew Walton 2019</p>
        <br/>
        <ul className="icons">
          <li><a href="https://soundcloud.com/user-415462540" className="icon fa-soundcloud" target="_blank"><span className="label">Soundcloud</span></a></li>
          <li><a href="https://www.instagram.com/matthewwalton01/" className="icon fa-instagram" target="_blank"><span className="label">Instagram</span></a></li>
          <li><a href="https://www.facebook.com/profile.php?id=100009553473883" className="icon fa-facebook" target="_blank"><span className="label">Facebook</span></a></li>
        </ul>
    </footer>
)

Footer.propTypes = {
    timeout: PropTypes.bool
}

export default Footer
