import React from 'react'
import PropTypes from 'prop-types'

const Header = (props) => (
    <header id="header" style={props.timeout ? {display: 'none'} : {}}>
        <div className="logo">
            <span className="icon fa-headphones"></span>
        </div>
        <div className="content">
            <div className="inner">
                <h1>Matthew Walton</h1>
                <p>I am a composer from the Space Coast. Check out some of my <a href="javascript:;" onClick={() => {props.onOpenArticle('work')}}>Music</a> here,<br />
                or visit my <a href="https://soundcloud.com/cactusheadmusic" target="_blank">Soundcloud</a> for more of my latest work.</p>
            </div>
        </div>
        <nav>
            <ul>
                <li><a href="javascript:;" onClick={() => {props.onOpenArticle('work')}}>Music</a></li>
                <li><a href="javascript:;" onClick={() => {props.onOpenArticle('about')}}>About</a></li>
                <li><a href="javascript:;" onClick={() => {props.onOpenArticle('contact')}}>Contact</a></li>
            </ul>
        </nav>
    </header>
)

Header.propTypes = {
    onOpenArticle: PropTypes.func,
    timeout: PropTypes.bool
}

export default Header
